.navbar li {
    display: inline-block;
    width: 33.3%;
    text-align: center;
    margin-top: 0px;
    font-family: serif
  }
  
  .navbar {
      background: #68a1b8;
      min-height: 70px;
      position: fixed;
      margin-top: 0px;
      width: 100%;
      -webkit-box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
      box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
      color: #fff;
      font-size: calc(10px + 14 * (100vw - 320px)/ 800);
      z-index: 100;
      white-space: nowrap;
  }